<template>
    <div class="container">
        <v-back title="宿管综合" />
        <md-field>
            <div class="title flex space-between align_center">
                <div class="flex align_center" style="margin-right: 1vw;"><md-icon name="home" class="margin_right_space color-1"></md-icon> {{$route.query.buildingName}}【{{typeArr[$route.query.type]}}】{{total}}人</div>
                <div class="flex align_center" style="width: 25vw;"><md-icon name="time" class="margin_right_space color-1"></md-icon> {{$route.query.time}}</div>
            </div>
            <md-field-item title="选择学院" :content="selectorValue3" @click="showSelector3" arrow solid />
        </md-field>
        <md-selector v-model="isSelectorShow3" :data="data3" @confirm="onSelectorConfirm3" min-height="320px" max-height="320px" title="选择学院" okText="确认" large-radius></md-selector>
        <div class="table">
            <div class="table_tr flex space-between border-bottom">
                <div class="table_th" style="width: 10%;">姓名</div>
                <div class="table_th">学号</div>
                <div class="table_th" style="width: 30%;">宿舍房间</div>
                <div class="table_th" style="width: 30%;">院系/班级</div>
                <div class="table_th" style="width: 10%;">状态</div>
            </div>
            <div class="table_body">
                <div class="table_tr flex space-between border-bottom" v-for="(item,index) in list" :key="index">
                    <div class="table_td" style="width: 12%;text-decoration: underline;" @click="fullShow(item)">{{item.username}}</div>
                    <div class="table_td" style="max-width: 20%;color: #1acfc6;">{{item.usernbr}}</div>
                    <div class="table_td" style="width: 30%;">{{item.building_name}}{{item.bed_nbr}}</div>
                    <div class="table_td" style="width: 30%;color: #1acfc6;">{{item.college_name}}{{item.org_names}}{{item.class_names}}</div>
                    <div class="table_td" style="width: 8%;">{{ item.entry_exit_type == '2' ? '未归' : item.entry_exit_type == '1' ? '在寝' : '无记录' }}</div>
                </div>
                <div v-show="list.length == 0" class="no_data">
                    <md-result-page text="没内容？切换学院试一试！"></md-result-page>
                </div>
            </div>
        </div>
        <md-landscape v-model="showFullScreen" :full-screen="false">
            <div style="text-align: left;padding: 4vw;font-size: 4vw;line-height: 6vw;">
                <div style="margin-bottom: 4vw;">详细信息</div>
                <div class="flex align_center">
                    <img v-if="showData.photo" :src="showData.photo" alt="" style="width: 30vw;min-height: 30vw;margin-right: 4vw;display: block;">
                    <img v-else src="@/assets/student.png" alt="" style="width: 30vw;min-height: 30vw;margin-right: 4vw;display: block;">
                    <div style="color: #2f86f6;">{{showData.entry_exit_status == null ? '未记录' : showData.entry_exit_status == '0' ? '正常' : '未记录'}} | {{showData.in_dorm_status == null ? '--' : showData.in_dorm_status == '0' ? '未归' : showData.in_dorm_status == '1' ? '在寝' : '未知'}}</div> 
                </div>
                <div>学生姓名：{{showData.username || showData.student_name}}</div>
                <div>学生学号：{{showData.usernbr || showData.student_nbr}}</div>
                <div>学院名称：{{showData.org_names}}</div>
                <div>班级名称：{{showData.class_names || '--'}}</div>
                <div>住宿信息：{{showData.building_name}}{{showData.dorm_nbr}}{{showData.bed_nbr}}</div>
                <div>最后通行：{{showData.entry_exit_time || '--'}}</div>
            </div>
            <div class="flex btns">
                <div>
                    <div @click="statusChange(1)">在寝</div>
                </div>
                <div>
                    <div @click="statusChange(0)">未归</div>
                </div>
            </div>

        </md-landscape>
    </div>
</template>

<script>
    export default {
        name: 'report_stulist',
        data() {
            return {
                typeArr: ['学生总数量','全部通行记录','24-72小时未归寝人员','72小时以上未归寝人员','24小时以上未出寝人员','闭寝时未归人员','闭寝后晚归人员','闭寝后早出人员'],
                list: [],
                selectorValue3: '',
                value3: '',
                isSelectorShow3: false,
                data3: [],
                total: 0,
                showFullScreen: false,
                showData: {

                }
            }
        },
        methods: {
            showSelector3() {
                this.isSelectorShow3 = true
            },
            onSelectorConfirm3({text, value}) {
                this.value3 = value
                this.selectorValue3 = text
                this.getList()  
            },
            //获取学院
            getDepartment() {
                this.$getL('/college/department', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id
                }, (res)=>{
                    this.data3 = [{
                        value: 'all',
                        text: '全部'
                    }]
                    res.data.forEach((item) => {
                        this.data3.push({
                            value: item.orgId,
                            text: item.orgName
                        })
                    })
                    this.getList()
                })
            },
            getList() {
                this.$getL('/college/stulist', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id,
                    type: this.$route.query.type,
                    buildingId: this.$route.query.buildingId,
                    college_id: this.value3,
                    entry_exit_time: this.$route.query.time
                }, (res)=>{
                    console.log(res)
                    this.list = res.data
                    this.total = res.total
                    this.$toast.hide()
                })
            },
            fullShow(itm) {
                this.showData = itm
                if (this.$route.query.type == 0) {
                    this.showFullScreen = true
                } else {
                    //
                    this.$getL('/college/stu_last_person', {
                        student_nbr: this.showData.usernbr
                    }, (res)=>{
                        console.log(res)
                        if (res.data) {
                            this.showData = res.data
                        }
                        this.showFullScreen = true
                        this.$toast.hide()
                    })
                }
                
            },
            //改变状态
            statusChange(status) {
                this.$getL('/dorm/indormstatus_change', {
                    userId: JSON.parse(localStorage.getItem('user')).user_id,
                    studentNbr: this.showData.usernbr,
                    status: status
                }, (res)=>{
                    console.log(res)
                    this.$toast.succeed('修改成功')
                    this.showData.in_dorm_status = status
                    this.$toast.hide()
                })
            },
        },
        mounted() {
            this.getDepartment()
            this.selectorValue3 = '全部'
            this.value3 = 'all'
        }
    }
</script>

<style scoped>
    .title {
        border-bottom: 1px solid #e2e4ea;
        padding: 0 0 4vw;
        font-size: 3.5vw;
    }
    .margin_right_space {
        margin-right: 1vw;
    }
    .table {
        font-size: 3.5vw;
        background-color: #fff;
        padding: 0 4vw 4vw;
    }
    .table_body {
        height: calc(100vh - 56vw);
        overflow-y: auto;
    }
    .table_th {
        width: 20%;
        text-align: center;
        padding: 2vw 0;
    }
    .table_td {
        width: 20%;
        text-align: center;
        padding: 2vw 0;
        word-wrap: break-word;
    }
    .no_data {
        margin-top: 20vw;
    }
    .btns {
        flex-wrap: wrap;
    }
    .btns > div {
        width: 50%;
        min-width: 50%;
        margin-bottom: 4vw;
    }
    .btns > div div {
        width: 60%;
        margin: 0 auto;
        border: 1px solid #ccc;
        padding: 2vw;
        border-radius: 2vw;
    }
</style>